import React, { useState } from "react";
import {
  Carousel,
  CarouselItem,
  CarouselControl,
  CarouselIndicators,
  CarouselCaption,
  Container
} from "reactstrap";
import "../css/carousel/index.css";
import eat from "../images/eat.png";
import food from "../images/food.png";
import tri from "../images/tri.png";
import weather from "../images/weather.png";
import work from "../images/work.png";

const items = [
  {
    src: eat,
    altText: "Slide 1",
    caption: "Eat-Da-Burger",
    link: "https://eat-da-burger-jp.herokuapp.com/",
    desc:
      "A fun web app that allows the user to add a burger to the menu, and then devour it! This app features MYSQL and Express."
  },
  {
    src: food,
    altText: "Slide 2",
    caption: "Foodecipe",
    link: "https://mustafakhairalla.github.io/Foodecipe/?",
    desc:
      "Foodecipe is a hunger-driven application designed to make finding delicious recipes from around the world easier than ever! Simply open Foodecipe and select a type of food from the drop-down menu. Once a choice is made, the user will be rewarded with six delicious recipes that populate on the screen."
  },
  {
    src: tri,
    altText: "Slide 3",
    caption: "TriPoints",
    link: "https://tripoint.herokuapp.com/",
    desc:
      "A beautiful web app providing users with the true costs of owning a brand-new Mercedes-Benz vehicle. The user is presented with fuel costs, services costs, and start prices for all models in the Mercedes-Benz lineup."
  },
  {
    src: weather,
    altText: "Slide 4",
    caption: "Weather Dashboard",
    link: "https://jcbpetersen1995.github.io/Weather-Dashboard-Jp/",
    desc:
      "The weather dashboard is simple application designed to show the user weather from around the world. Simply search for a city, and the weather information is populated. The app will display the current weather situation, as well as a five-day forecast. There are also several buttons with popular cities to choose from. Data gathered using AJAX calls to various API's."
  },
  {
    src: work,
    altText: "Slide 5",
    caption: "Workday Scheduler",
    link: "https://jcbpetersen1995.github.io/unit-05-homework/",
    desc:
      "This application is designed to make tasks throughout the day easier for this user. Simply type in a task and select the save button to save. The timeblocks are color-coded to show you what tasks still need to be done, based on the time of day. Gray is the past, green is the future, and red is the present. Please enjoy."
  },
  {
    src:
      "https://github.com/jcbpetersen1995/Employee_Directory_JP/raw/gh-pages/Screen%20Shot%202020-05-07%20at%2010.15.16%20PM.png?raw=true",
    altText: "Slide 6",
    caption: "Employee Directory",
    link: "https://github.com/jcbpetersen1995/Employee_Directory_JP",
    desc:
      "A React application that allows the user to view a list of employees by state, or sort by last name. The data for this app is sourced from randomuser.me."
  }
];
const CarouselBlock = () => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [animating, setAnimating] = useState(false);

  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  };

  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  };

  const goToIndex = newIndex => {
    if (animating) return;
    setActiveIndex(newIndex);
  };

  const slides = items.map(item => {
    return (
      <CarouselItem
        onExiting={() => setAnimating(true)}
        onExited={() => setAnimating(false)}
        key={item.src}
      >
        <a target="_blank" href={item.link}>
          <img src={item.src} alt={item.altText} className="carouselImage" />
        </a>
        <CarouselCaption
          className="caption"
          captionText={item.desc}
          captionHeader={item.caption}
        />
      </CarouselItem>
    );
  });

  return (
    <Container className="carouselBlock">
      <h1 className="portfolioHeader rainbowText">PORTFOLIO</h1>
      <Carousel activeIndex={activeIndex} next={next} previous={previous}>
        <CarouselIndicators
          className="indicators"
          items={items}
          activeIndex={activeIndex}
          onClickHandler={goToIndex}
        />
        {slides}
        <CarouselControl
          className="left"
          direction="prev"
          directionText="Previous"
          onClickHandler={previous}
        />
        <CarouselControl
          className="right"
          direction="next"
          directionText="Next"
          onClickHandler={next}
        />
      </Carousel>
    </Container>
  );
};

export default CarouselBlock;
